import React, { useEffect, useState } from 'react';
import axios from 'axios';
import store from 'store';
import { backendUrl } from '../../../config/config';

import Spinner from '../../../components/Spinner/index';
import ErrorIndicator from '../../../components/Errors/ErrorIndicator/index';
import Modal from '../../../components/UI/Modal/Modal';

import './index.scss';
import {useCustomStore} from "../../../context/useStore";
import Basket from "../../../components/Basket";
import NumberFormat from 'react-number-format';

const HeaderBlock = (props) => {
    const { state } = useCustomStore();

    const [ phone, setPhone ] = useState('');
    const [ countBasket, setCountBasket ] = useState(0);
    const [ modalSuccess, setModalSuccess ] = useState(false);
    const [ loading, setLoading ] = useState(false);
    const [ error, setError ] = useState(false);


    useEffect(() => {
        setCountBasket(
            state.cart.basket!== undefined &&
            state.cart.basket.find(el => el.id === props.food._id) ?
            state.cart.basket.filter(el1 => el1.id === props.food._id)[0].countBasket : 1
        );
    }, []);

    const orderGood = () => {
        setLoading(true);

        const items = [{
            product: props.food._id,
            quantity: countBasket
        }];

            axios.post(backendUrl + "/order/notAuth", {
                oplata: {
                    type: "cash",
                    status: "not-paid",
                  },
                delivery: {
                status: "courier",
                },
                city: store.get("24rolls").customOptions.city,
                address: {
                    text: '_',
                    city: store.get("24rolls").customOptions.city,
                },
                name: '-',
                phone: phone,
                comment: '',
                sdachaS: '',
                cutlery: 0,
                items: items,
                email: '',
                bonus: 0,
            })
            .then(res => {
                setPhone('');
                setModalSuccess(true);
                setCountBasket(1);
                setLoading(false);
                setError(false);
            })
            .catch(err => {
               setLoading(false);
               setError(true);
            });
    };

    const modalContent = (
        <div style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            textAlign: 'center'
        }}>
            <p style={{
                color: "green",
                textAlign: "center",
                fontWeight: "700",
                fontSize: "24px",
            }}>
                {'Ваш заказ находиться в обработке'}
            </p>
        </div>
    );

    return (
        <div
            className={'header-slider'}
            key={props.food._id}
        >
            <Modal
                show={modalSuccess}
                modalClosed={() => setModalSuccess(false)}
            >
                {modalContent}
            </Modal>
            {loading && <Spinner />}
            {error && <ErrorIndicator />}
            {!loading && !error &&
                <>
                    <div className={'header-slider_list-block'}>
                        <div className={'list-content'}>
                            <div className={'list-content_header'}>
                                <h1>{props.food.name_ru}</h1>
                                <p>Вес {props.food.weight} г.</p>
                                <span>{props.food.description_ru}</span>
                            </div>
                            <div className={'list-content_center'}>
                                <button
                                    onClick={() => setCountBasket(countBasket === 1 ? 1 : countBasket - 1)}
                                >
                                    -
                                </button>
                                <span><p> {countBasket} </p></span>
                                <button
                                    onClick={() => setCountBasket(countBasket + 1)}
                                >
                                    +
                                </button>
                            </div>
                            <div className={'list-content_footer'}>
                                <div className={'price'}>
                                    <h2>
                                        {props.food.percent !== 0 ? 
                                        (props.food.price * ( (100 - props.food.percent) / 100)).toFixed(1) : props.food.price} 
                                        грн.
                                    </h2>
                                    {
                                        props.food.percent !== 0 && <p>{props.food.price} грн.</p>
                                    }
                                </div>
                                <Basket 
                                    {...props} 
                                    count={countBasket}
                                    food={props.food}
                                    title={'button.toOrder'} 
                                    style={'open-modalTwo'} 
                                />
                                <div className={'phone-input'}>
                                    <NumberFormat 
                                        format="+38(0##) ### ## ##" 
                                        mask="_"
                                        allowEmptyFormatting
                                        value={phone} 
                                        onChange={(event)=> setPhone(event.target.value)}
                                    />
                                    <button 
                                        onClick={orderGood}
                                        className={'price-button'}
                                    >
                                        {'Заказать в 1 клик'}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={'header-slider_image-block'}>
                        <img
                            src={backendUrl + `/${props.food.image}`}
                            alt={props.food.name}
                        />
                    </div>
                </>
            }
        </div>
    )
}

export default HeaderBlock;